const randAnimal = () => animals[Math.floor(Math.random() * animals.length)];

const Cautare = ({ dark, inchide }) => {
  const [input, setInput] = useState("");

  const [rezultate, setRezultate] = useState([]);
  const [rezultateIS, setRezultateIS] = useState([]);
  const [cats, setCats] = useState(randAnimal());

  const classes = useStyles();
  let bgSearch = dark ? "#262B36" : "#fefefe";
  useEffect(() => {
    if (input !== "") {
      /// blog
      const apiUrl = `${local}?cautare=${input}`;
      axios.get(apiUrl).then((res) => {
        let rez = res.data;
        setRezultate(rez.rezultate);
      });

      const apiUrl2 = `${local}?cautare_info_studenti=${input}`;
      axios.get(apiUrl2).then((res) => {
        let rez = res.data;
        setRezultateIS(rez.rezultate);
      });
    }
  }, [input]);

  return (
    <>
      <Helmet>
        <title>{`CƒÉutare - OSUT`}</title>
      </Helmet>
      <div
        id="Cautare"
        style={{
          zIndex: 9999,
          width: "100vw",
          position: "fixed",
          top: 0,
          left: 0,
          height: "100vh",
        }}>
        <Paper>
          <AppBar
            position="fixed"
            color="dark"
            style={{
              background: bgSearch,
              borderBottom: "1px solid rgba(125,125,165,.1)",
              boxShadow: "none",
              backdropFilter: "blur(14px)",
            }}>
            <Container maxWidth="lg">
              <Toolbar disableGutters={true} variant="regular">
                <div className={classes.root}>
                  <InputBase
                    className={classes.input}
                    placeholder="CautƒÉ articole"
                    inputProps={{ "aria-label": "cauta articole" }}
                    fullWidth={true}
                    autoFocus={true}
                    onChange={(e) => setInput(e.target.value)}
                  />
                  {/* <IconButton type="submit" className={classes.iconButton} aria-label="search">
        <SearchIcon />
      </IconButton>
      <Divider className={classes.divider} orientation="vertical" /> */}
                  <IconButton
                    color="default"
                    className={classes.iconButton}
                    aria-label="inchide"
                    onClick={() => inchide()}>
                    <CloseIcon />
                  </IconButton>
                </div>
              </Toolbar>
            </Container>
          </AppBar>

          <Container
            maxWidth="lg"
            style={{
              marginTop: 58,
              height: "100vh",
              overflowY: "auto",
              paddingTop: 20,
            }}>
            <Grid
              container
              spacing={0}
              direction="row"
              alignItems="stretch"
              alignContent="stretch">
              {rezultate.length !== [] && input.toLowerCase() == "meow" && (
                <h1
                  style={{ userSelect: "none" }}
                  onClick={() => {
                    setCats(cats + "" + randAnimal());
                  }}>
                  {cats}
                </h1>
              )}
              {rezultate.length !== [] && rezultate.length > 0 && input !== "" && (
                <Typography
                  color="primary"
                  variant="h5"
                  style={{
                    fontWeight: 300,
                    textTransform: "uppercase",
                    marginTop: 20,
                    marginBottom: 20,
                  }}>
                  Articole:
                </Typography>
              )}

              {rezultate.length !== [] &&
                input !== "" &&
                rezultate.map((item) => (
                  <Grid
                    key={item.id}
                    item
                    xs={12}
                    sm={12}
                    className={classes.card}
                    style={{ marginBottom: "10px" }}>
                    <Link
                      to={`/p/${item.urlfriendly}`}
                      onClick={() => inchide()}>
                      <Typography
                        color="textSecondary"
                        variant="body1"
                        style={{
                          fontSize: "1.3rem",
                          fontWeight: 100,
                          padding: "5px 0px",
                          borderBottom: "1px solid rgba(180,170,170,.1)",
                          marginTop: "0px",
                        }}
                        dangerouslySetInnerHTML={{ __html: item.titlu }}
                      />
                    </Link>
                  </Grid>
                ))}
              {rezultateIS.length !== [] &&
                rezultateIS.length > 0 &&
                input !== "" && (
                  <Typography
                    color="primary"
                    variant="h5"
                    style={{
                      fontWeight: 300,
                      textTransform: "uppercase",
                      marginTop: 40,
                      marginBottom: 20,
                    }}>
                    Info studen»õi:
                  </Typography>
                )}
              {rezultateIS.length !== [] &&
                input !== "" &&
                rezultateIS.map((item) => (
                  <Grid
                    key={item.id}
                    item
                    xs={12}
                    sm={12}
                    className={classes.card}
                    style={{ marginBottom: "10px" }}>
                    <Link
                      to={`/informatii-studenti/${item.linkCategorie}/${item.link}`}
                      onClick={() => inchide()}>
                      <Typography
                        color="textSecondary"
                        variant="body1"
                        style={{
                          fontSize: "1.3rem",
                          fontWeight: 100,
                          padding: "5px 0px",
                          borderBottom: "1px solid rgba(180,170,170,.1)",
                          marginTop: "0px",
                        }}
                        dangerouslySetInnerHTML={{ __html: item.intrebare }}
                      />
                    </Link>
                  </Grid>
                ))}
              <AdditionalResults input={input} />
              <Grid
                key="paddingBottom"
                item
                xs={12}
                sm={12}
                className={classes.card}
                style={{ paddingBottom: 150 }}></Grid>
            </Grid>
          </Container>
        </Paper>
      </div>
    </>
  );
};

export default Cautare;
